// Тёмная тема спиннера

/* Стиль кнопки (перемикача)*/
.change_theme {
  border: 1px solid #b92f2c;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: 1px 4px 6px 0px rgb(255 255 255 / 16%);
  fill: #b92f2c;
  background-color: transparent;
  stroke: #b92f2c;

  &:hover,
  &:focus {
    stroke: #fff;
    fill: #fff;
    border-color: #fff;
  }
}

button.change_theme[data-theme='onLight'] {
  display: block;
}

button.change_theme[data-theme='onDark'] {
  display: none;
}

body {
  background-color: #1d1917;
}

// стилі для галиреї фільмів

.movie-card {
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    box-shadow: 0 5px 9px 4px #8c8c8c;
  }
}

.movie-card__name {
  color: #fff;
}

// стилі для прокрутки

.icon-to-top {
  fill: #b92f2c;
  transition: all 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.btn-to-top:hover .icon-to-top {
  fill: #fff;
}

// стилі для футера

.footer {
  background-color: #545454;
}

.footer-text {
  color: #f7f7f7;
}

.team-modal {
  color: #f7f7f7;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    color: #b92f2c;
  }
}

// стилі для модального вікна

.backdrop {
  background-color: rgba(29, 25, 23, 0.8);
}

.modal {
  background-color: #545454;
}

.close-btn {
  stroke: #fff;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus .btn-close__icon {
    stroke: #b92f2c;
  }
}

.modal__title {
  color: #fff;
}

.charact__name {
  color: #fff;
}

.charact__value {
  color: #f7f7f7;
}

.charact__value > span {
  color: #b92f2c;
}

.modal__about {
  color: #f7f7f7;
}

.modal__button {
  color: #f7f7f7;
  border: 1px solid #b92f2c;
}

// стилі для командної модалки

.team-wrapper {
  background-color: #545454;
}

.team-name {
  color: #fff;
}

.team-role {
  color: #f7f7f7;
}

