.change_theme {
  cursor: pointer;
  fill: #b92f2c;
  stroke: #b92f2c;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #b92f2c;
  border-radius: 5px;
  box-shadow: 1px 4px 6px rgba(255, 255, 255, .16);
}

.change_theme:hover, .change_theme:focus {
  stroke: #fff;
  fill: #fff;
  border-color: #fff;
}

button.change_theme[data-theme="onLight"] {
  display: block;
}

button.change_theme[data-theme="onDark"] {
  display: none;
}

body {
  background-color: #1d1917;
}

.movie-card {
